<template>
  <div class="maps-container">
    <div id="main" style="width:100%; height:100%"></div>
  </div>
</template>
<script>
import "echarts/map/js/province/yunnan.js";
import obj from "echarts/map/json/province/yunnan.json";
export default {
  data() {
    return {
      listArr: [], //城市json
      max: "", //最大value值
      min: "" // 最小value值
    };
  },
  created() {
    this.getData();
  },
  mounted() {
    this.DrawMap();
  },
  methods: {
    getData() {
      // 获取城市名称数据
      console.log("取到的云南省的json数据", obj);
      if (obj) {
        let arr = obj.features;
        // 循环取出 城市名称和value值
        for (var j = 0; j < arr.length; j++) {
          this.max = arr[0].id;
          this.min = arr[0].id;
          if (arr[j].id > this.max) {
            this.max = arr[j].id;
          }
          if (arr[j].id < this.min) {
            this.min = arr[j].id;
          }
          this.listArr.push({
            name: arr[j].properties.name,
            value: arr[j].id
          });
        }
      }
    },
    DrawMap() {
      let _this = this;
      // console.log(obj);
      this.$echarts.registerMap("yunan", obj); // 注册一个地图，名字叫shenzhen
      let myChart = this.$echarts.init(document.getElementById("main"));
      var geoCoordMap = {
        昆明市中医院: [102.712251, 25.040609],
        昭通市人民医院: [103.717216, 27.336999],
        红河哈尼族彝族自治州: [103.384182, 23.366775],
        德宏傣族景颇族自治州: [98.578363, 24.436694]
      };
      var convertData = function(data) {
        var res = [];
        for (var i = 0; i < data.length; i++) {
          var geoCoord = geoCoordMap[data[i].name];
          if (geoCoord) {
            res.push({
              name: data[i].name,
              value: geoCoord.concat(data[i].value)
            });
          }
        }
        return res;
      };
      let option = {
        visualMap: {
       // 地图显示不同的颜色
          min: _this.min,
          max: _this.max,
          show: false,
          inRange: {
            color: ["lightskyblue", "yellow", "orangered"]
          }
        },
        geo: {
          // 地理坐标系组件
          map: "yunan",
          itemStyle: {
            normal: {
              areaColor: "#e8f6ff",
              borderColor: "#4a9dc9"
            }
          }
        },

        series: [
          {
            type: "map",
            map: "云南",
            itemStyle: {
              normal: { label: { show: true } },
              emphasis: { label: { show: true } },
              emphasis: {
                areaColor: "#67C23A" //鼠标进入时的颜色
              }
            },
            data: _this.listArr
          },
          {
            type: "effectScatter", // 散点图类型
            coordinateSystem: "geo", // 使用地理坐标系
            data: convertData([
              { name: "昆明市中医院", value: [10] }, // value的值是随便写的，不用理会
              { name: "昭通市人民医院", value: [20] },
              { name: "红河哈尼族彝族自治州", value: [30] },
              { name: "德宏傣族景颇族自治州", value: [40] }
            ]),
            symbolSize: 15, // 散点的大小
            label: {
              // 散点图上显示文字
              normal: {
                formatter: "{b}",
                position: "right",
                show: true
              }
            },
            itemStyle: {
              normal: {
                color: "#67C23A" // 散点的颜色
              }
            }
          }
        ]
      };
      myChart.setOption(option);
    }
  }
};
</script>
<style scoped lang="scss">
.maps-container {
  width: 100%;
  height: 100%;
}
</style>
